*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-section-bg: #ffb8ca;
  --headerBg: rgba(255, 184, 202, .486);
  --second-section-bg: #fff;
  --third-section-bg: #fff5f6;
  --review-bg: #fafafa;
  --review-bg-clear: rgba(250, 250, 250, 0);
  --review-bg-lines: #e1e1e1;
  --font-color-one: #fff;
  --font-color-two: #d41443;
  --font-color-three: #907e82;
  --font-color-four: #0000000;
  --font-color-five: #ffa5ba;
  --secton_side_padding: 20px;
  --secton_tablet_padding: 34px;
  --secton_desktop_padding: 100px;
  --animationDuration: 1s;
  --transitionDuration: 500ms;
  --btnShadow: 0px 4px 4px rgba(0, 0, 0, .25);
}

.btns {
  column-gap: 8px;
  display: flex;
}

.btn {
  background-color: var(--font-color-one);
  color: var(--font-color-two);
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 20px;
  padding: 9px 13px;
  font-family: DM Sans;
  font-size: 14px;
  overflow: hidden;
}

.btn a {
  letter-spacing: 0;
  color: var(--font-color-two);
  font-size: 8px;
  font-weight: 400;
  line-height: 10px;
}

.btn:hover, .btn:focus {
  box-shadow: var(--btnShadow);
  background-color: var(--font-color-two);
  color: var(--font-color-one);
  transform: scale(.95);
}

.btn:hover a, .btn:focus a {
  color: var(--font-color-one);
}

.btn:hover svg, .btn:focus svg {
  fill: var(--font-color-one);
  box-shadow: none;
  stroke: var(--font-color-one);
}

.to_top {
  z-index: 10;
  width: 40px;
  height: 40px;
  text-align: center;
  color: var(--font-color-one);
  box-shadow: 0 0 15px 10px #fff, 0 0 18px 1px var(--font-color-two);
  opacity: 0;
  transition-duration: var(--animationDuration);
  background-color: rgba(212, 20, 67, .39);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-size: 8px;
  font-weight: bold;
  display: flex;
  position: fixed;
  bottom: -50px;
  right: 40px;
}

.to_top-visible {
  opacity: 1;
  bottom: 40px;
}

.to_top:hover, .to_top:focus {
  background-color: rgba(212, 20, 68, .77);
  border: 1px solid #fff;
  transform: scale(1.1);
}

.form__btn {
  background-color: var(--font-color-two);
  color: var(--font-color-one);
}

*, :before, :after {
  transition-duration: var(--transitionDuration);
}

html {
  scroll-behavior: smooth;
  height: 100%;
}

body {
  min-height: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.stop__scroll {
  overflow: hidden;
}

section {
  padding-left: var(--secton_side_padding);
  padding-right: var(--secton_side_padding);
  width: 100vw;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding-top: 120px;
  padding-bottom: 100px;
  display: flex;
}

img, p, h1, h2, h3, h4, h5, svg {
  margin: 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
}

a {
  text-decoration: none;
  display: block;
}

.section__title {
  letter-spacing: .04em;
  text-align: center;
  color: var(--font-color-two);
  margin-bottom: 31px;
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.12;
}

.section__review {
  text-align: center;
  color: var(--main-section-bg);
  margin-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.17;
}

.hidden__title {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  section {
    padding-left: var(--secton_tablet_padding);
    padding-right: var(--secton_tablet_padding);
  }

  .section__title {
    margin-bottom: 40px;
    font-size: 48px;
  }

  .section__review {
    font-size: 26px;
  }
}

@media screen and (min-width: 1200px) {
  section {
    padding-left: var(--secton_desktop_padding);
    padding-right: var(--secton_desktop_padding);
    padding-top: 130px;
    padding-bottom: 120px;
  }

  .section__title {
    margin-bottom: 50px;
    font-size: 58px;
  }

  .section__review {
    font-size: 30px;
  }
}

.modal {
  width: 100vw;
  height: 100vh;
  z-index: 200;
  opacity: 0;
  visibility: hidden;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0), rgba(23, 22, 22, .09)), url("modalBg.2d17df1e.jpeg");
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  padding: 10%;
  transition: opacity 1s linear, transform;
  display: flex;
  position: fixed;
  overflow: auto;
}

.modal__visible {
  opacity: 1;
  visibility: visible;
}

.modal__content {
  background-color: var(--second-section-bg);
  text-align: center;
  max-height: 90vh;
  border-radius: 16px;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding: 50px;
  display: flex;
  position: relative;
  box-shadow: 0 0 9px rgba(0, 0, 0, .33);
}

.modal__content .modal__close {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: -30px;
  right: 0;
}

.modal__content .modal__close svg {
  width: 14px;
  height: 14px;
  stroke: var(--font-color-one);
}

.modal__content .product__img {
  margin-bottom: 30px;
}

.modal__content h4 {
  color: #ffa5ba;
  font-family: Titan One;
}

.modal__content .product__name {
  margin-bottom: 45px;
}

.modal__content .product__name:after {
  content: ". . .";
  color: #ffa5ba;
  letter-spacing: 0;
  text-align: center;
  top: 26px;
}

.modal p {
  color: #ffa5ba;
  width: fit-content;
  height: inherit;
  margin-bottom: 0;
  font-family: Titan One;
  overflow-y: auto;
}

.discr__wrapper {
  max-height: 250px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.discr__wrapper p {
  line-height: 1;
  overflow: auto;
}

.product__description {
  overflow-y: auto;
}

.cow img {
  width: 170px;
}

.cow p {
  max-height: 300px;
}

.location__content {
  padding: 0;
}

.franchise {
  row-gap: 20px;
}

.franchise h4 {
  letter-spacing: 1px;
  text-shadow: 2px 2px 1px #000, 8px 9px 5px #fff;
  font-size: 26px;
}

.franchise p {
  color: var(--font-color-three);
  font-family: DM Sans;
  font-weight: 900;
}

.modal__form {
  flex-direction: column;
  row-gap: 30px;
  display: flex;
}

.modal__form-input, .modal__form-textarea {
  border: none;
  border-bottom: 1px solid var(--font-color-three);
  color: var(--font-color-three);
  background-color: rgba(0, 0, 0, 0);
}

@media screen and (max-width: 425px) {
  .product__wpapper img {
    max-width: 160px;
    transition-duration: var(--transitionDuration);
  }
}

.franchise {
  width: 240px;
  height: 125px;
  background-image: url("franchise.0746c565.jpeg"), linear-gradient(rgba(0, 0, 0, .49), rgba(0, 0, 0, .49));
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 0;
}

@media screen and (min-width: 768px) {
  .franchise {
    width: 480px;
    height: 252px;
  }
}

@media screen and (min-width: 1200px) {
  .franchise {
    width: 720px;
    height: 377px;
  }
}

@media screen and (min-width: 768px) {
  .modal__content .discr__wrapper {
    flex-direction: column;
    align-items: center;
    margin-left: 20px;
    display: flex;
  }

  .product__wpapper {
    flex-direction: row;
  }

  .cow p {
    max-height: 250px;
  }
}

header {
  width: 100vw;
  padding: 6px var(--secton_side_padding);
  z-index: 105;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  animation-name: heade__logo;
  animation-duration: var(--animationDuration);
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}

@keyframes heade__logo {
  0% {
    opacity: 0;
    margin-left: -300px;
  }

  100% {
    opacity: 1;
    margin-left: 0;
  }
}

.header__nav {
  column-gap: 35px;
  display: none;
}

.header__nav-item a {
  letter-spacing: 0;
  color: var(--font-color-one);
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
}

.header__btns {
  column-gap: 30px;
  display: flex;
}

.burger__button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: all 1s;
}

.burger__button:focus, .burger__button:hover {
  rotate: 90deg;
  scale: 110%;
}

.burger__button svg {
  width: 20px;
  height: 12px;
}

.header__btn {
  width: 160px;
  height: 44px;
  letter-spacing: 0;
  opacity: 0;
  justify-content: center;
  align-items: center;
  column-gap: 12px;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.header__btn svg {
  width: 10px;
  height: 10px;
  stroke: var(--font-color-two);
}

.header__btn:focus svg, .header__btn:hover svg {
  stroke: var(--font-color-one);
}

.header__nav, .burger__menu, .header__btn {
  animation-name: burger__menu;
  animation-duration: var(--animationDuration);
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}

@keyframes burger__menu {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 767px) {
  .header__btn {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  header {
    padding: 37px var(--secton_tablet_padding) 10px;
  }
}

@media screen and (min-width: 1200px) {
  header {
    padding: 37px var(--secton_tablet_padding) 10px;
  }

  .header__nav {
    display: flex;
  }

  .header__nav-linc:hover, .header__nav-linc:focus {
    color: var(--font-color-two);
  }

  .burger__button {
    display: none;
  }
}

.mobile__menu {
  z-index: 110;
  width: 250px;
  background-color: #e17992;
  padding-top: 60px;
  padding-bottom: 100vh;
  position: absolute;
  top: 0;
  right: -250px;
}

.mobile__menu-cross {
  background: none;
  border: none;
  padding: 0;
  position: absolute;
  top: 23px;
  right: 28px;
}

.mobile__menu-cross svg {
  width: 14px;
  height: 14px;
  stroke: #fff;
}

.mobile__menu-butn {
  width: 210px;
  height: 44px;
  justify-content: center;
  align-items: center;
  column-gap: 12px;
  margin-bottom: 19px;
  margin-left: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.mobile__menu-butn svg {
  width: 10px;
  height: 10px;
  stroke: var(--font-color-two);
}

.mobile__menu-nav li {
  border-bottom: 1px solid rgba(255, 255, 255, .133);
  padding-top: 13px;
  padding-bottom: 11px;
}

.mobile__menu-nav a {
  letter-spacing: 0;
  color: var(--font-color-one);
  margin-left: 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
}

.mobile__menu-nav li:hover a, .mobile__menu-nav li:focus a {
  color: var(--font-color-two);
  font-weight: bold;
}

.mobile__menu-visible {
  right: 0;
}

@media screen and (min-width: 1200px) {
  .mobile__menu {
    display: none;
  }
}

.hero__section {
  min-height: 500px;
  background-color: var(--main-section-bg);
  background-image: url("hero-elipse-big.e14bdfc0.png");
  background-repeat: no-repeat;
  background-size: 346px;
  align-items: flex-start;
  margin-top: -62px;
  padding-top: 182px;
  animation-name: bg__img;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  position: relative;
  overflow: hidden;
}

@keyframes bg__img {
  0% {
    background-position: right -347px bottom 75px;
  }

  100% {
    background-position: right -62px bottom 75px;
  }
}

.hero__title {
  text-align: left;
  color: var(--font-color-one);
  text-shadow: 3px 3px 1px rgba(0, 0, 0, .24);
  opacity: 0;
  animation-name: hero__title;
  animation-duration: var(--animationDuration);
  margin-bottom: 19px;
  font-size: 26px;
  line-height: 1.15;
  animation-timing-function: linear;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}

.hero__title span {
  color: var(--font-color-two);
  z-index: 10;
  font-size: 48px;
}

.hero__title-wrapper {
  z-index: 10;
}

@keyframes hero__title {
  0% {
    opacity: 0;
    left: -300px;
  }

  100% {
    opacity: 1;
    left: 20px;
  }
}

.hero__icecream {
  opacity: 0;
  width: 0;
  height: 0;
  animation-name: icecream;
  animation-duration: var(--animationDuration);
  animation-timing-function: linear;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  position: absolute;
  bottom: 100px;
}

.hero__icecream:hover {
  transform: scale(1.1);
}

@keyframes icecream {
  100% {
    opacity: 1;
    width: 171px;
    height: 410px;
    bottom: -7px;
  }
}

.hero__btn {
  opacity: 0;
  z-index: 100;
  padding: 9px 13px;
  animation-name: hero__btns;
  animation-duration: .3s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}

.hero__btn:nth-child(1) {
  animation-delay: 3.5s;
}

.hero__btn:nth-child(2) {
  animation-delay: 4s;
}

.hero__milk {
  display: none;
}

@keyframes hero__btns {
  0% {
    opacity: 0;
    scale: .1;
  }

  80% {
    opacity: 1;
    scale: 1.3;
  }

  100% {
    opacity: 1;
    scale: 1;
  }
}

.hero__milk-wrapper {
  display: none;
}

@keyframes icecream {
  0% {
    width: 11px;
    height: 11px;
    right: 132px;
  }

  100% {
    opacity: 1;
    width: 171px;
    height: 410px;
    position: absolute;
    bottom: -7px;
    right: 32px;
  }
}

.hero__stats {
  display: none;
}

